import React,{useState} from 'react'
import { FaBars } from 'react-icons/fa'
import {AiOutlineClose} from 'react-icons/ai'
import Modal from "../../commonComponents/Modal"
import {homeMenu} from "../../data"
export default function NavBar() {

    const NavLogo = FaBars
    const [enableNav,setEnableNav] = useState(false)
    const [enableNavBar,setEnanbleNavBar] = useState(false)

    const handleEnableNav = () => {
        document.body.style.overflow = "hidden";
        setEnableNav(true);
    }

    const handleDisableNav = () => {
        document.body.style.overflow = "auto";
        setEnableNav(false);
    }

    const handleNavBar = (e) => {
        e.preventDefault();
        setEnanbleNavBar(!enableNavBar);
    }

    return (
        <div className="header_navbar">
            <div className="header_navbar-logo-wrapper text-hv-primary" >         
                <NavLogo 
                onClick={handleEnableNav}
                />
            <div/>         
            <div className={`header_navbar-main ${enableNav ? "enable" : "disable"}`}>
                <div className="header_navbar-close"
                 onClick={handleDisableNav}
                >
                    <AiOutlineClose/>
                </div>
                <Modal
                onHandleDisableNav={handleDisableNav}
                />
                <div className="header_navbar-menu">
                        <ul className="header_navbar-list">
                            {homeMenu.map((item, index) => {
                                const {title,link,listStore} = item
                               
                                return (
                                    <li className="header_navbar-item" key={index}   onClick={listStore && handleNavBar}>
                                        <a href={link} className="header_navbar-link"
                                      
                                        >
                                            {title}
                                        </a>
                                        {listStore ?  
                                            <>
                                                <div className={`header_navbar-dropdown ${enableNavBar ? "rotate-180deg" : "" }`}>
                                                    {listStore.icon}
                                                </div>
                                                <ul className={`navbar_store-list ${enableNavBar ? "enable" : "disable"}`}>
                                                    {listStore.subList.map((item, index) => {
                                                        const {title,link} = item;
                                                        return (
                                                            <li className="navbar_store-item">
                                                                <a href={link} className="navbar_store-item-link"
                                                                >
                                                                    {title}
                                                                </a>
                                                            </li>
                                                        )
                                                    })}
                                                </ul>     
                                            </>
                                            : ""
                                        }              
                                    </li>
                                )
                            })}
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    )
}
